        flex-basis: 83.33333%;
        max-width: 83.33333%;
    }

    .s-11 {
        flex-basis: 91.66667%;
        max-width: 91.66667%;
    }

    .s-12 {
        flex-basis: 100%;
        max-width: 100%;
    }

    .s-0 {
        display: none !important;
    }
}

